import { UacStoreService } from '../../../store/app-config/uac/uac-store.service';
import { Component, OnInit, Output, EventEmitter, NgZone } from '@angular/core';
import { UacService } from './services/uac.service';
import { filter, map, takeWhile, skipWhile, mergeMap, tap } from 'rxjs/operators';
import { Call } from './services/ua.service/call';
import { IUacNewRTCSessionData, CallType } from './services/models';
import { CallspollService } from './services/callspoll.service';
import { Observable, of, BehaviorSubject } from 'rxjs';


@Component({
  selector: 'app-pbx-agent',
  templateUrl: './pbx-agent.component.html',
  styleUrls: ['./pbx-agent.component.scss']
})
export class PbxAgentComponent implements OnInit {

  isConnected$ = this.uacService.status;
  uacNoyifier$ = this.uacService.notifier;
  private alive = true;
  currentCall$: Observable<Call>;
  currentCall: Call;
  currentCallStatus$ = new BehaviorSubject<CallType | ''>('');

  constructor(
    private callsPool: CallspollService,
    private uacService: UacService,
    private ngZone: NgZone
  ) { }

  ngOnInit() {
    this.uacService.notifier.pipe(
      filter(uaMsg => uaMsg.event === 'newRTCSession'),
      map(uaMsg => uaMsg.data),
      takeWhile(() => this.alive)).
      subscribe((rtcData: IUacNewRTCSessionData) => {
        const call = new Call();
        call.setSession(rtcData);
        this.callsPool.addCall(call);
      });
    this.currentCall$ = this.callsPool.calls.asObservable().pipe(
      skipWhile(n => !n.length),
      tap(n => this.currentCall = n[0]),
      mergeMap((n) => of(n[0])),
    );
    this.currentCall$.pipe(
      
      mergeMap((n) => n.status)
    ).subscribe(n => {
      this.ngZone.run(() => this.currentCallStatus$.next(n.type));
    });

    this.currentCall$.pipe(
      mergeMap((n) => n.liveDuration)
    ).subscribe(n=>console.log(n));

  }

}
