import { Component, OnInit, Input, ContentChild } from '@angular/core';
import { Footer } from 'primeng/shared';


@Component({
  selector: 'app-layout-card',
  templateUrl: './layout-card.component.html',
  styleUrls: ['./layout-card.component.scss']
})
export class LayoutCardComponent implements OnInit {
  private _footerHeight = `calc(100% - 0px)`;

  @Input() header: string;

  @Input() footerHeight = `0px`;

  @Input() scrollable = true;

  get bodyHeight() {
    return `calc(100% - ` + this.footerHeight + `)`;
  }

  @ContentChild(Footer, { static: false }) footerFacet;

  constructor() { }

  ngOnInit() {

  }

}
